/**
 * 
 */
import React from "react";
import { memo, useState } from "react";
import { searchItems, sortTable } from "../pages/Common.js";
import vssDB from "./VssDB.json";
import vssAPI from "./VssAPI.json";
import { 
	SIGNAL_TYPES, SIGNAL_VIEWER_CHANNEL,
	setSignals,  
	subscribeSignals, unsubscribeSignals 
} from "./WsReq.js";

/* global BigInt */


const SENDER = "SENDER";
const RECEIVER = "RECEIVER";

const COLUMNS = {
	INDEX      : 0,
	VSS_KEY    : 1,
	SIGNAL     : 3,
	INIT_VALUE : 7,
	TYPE       : 9,
	SENDER     : 12,
	RECEIVER   : 13,
};

const DEFAULT_SEARCH_ITEMS = [ COLUMNS.SIGNAL, COLUMNS.VSS_KEY ];
const EXTRA_SEARCH_ITEMS = [ SENDER, RECEIVER ];
const EXTRA_SEARCH_INDICES = [ COLUMNS.SENDER, COLUMNS.RECEIVER ];

const SIGNAL_VIEWER_CHANNEL2 = "signalviewer2";


class SignalConversion extends React.Component {
	
	canSignalMap = null;
	canSignalToPduMap = null;

	vssSignalMap = new Map();
	canToVssMap = new Map();
	vssToCanMap = new Map();

	vssSignals = [];

	vssApiMap = new Map();

	constructor(props) {
		super(props);

		this.canSignalMap = props.signalMap;
		this.canSignalToPduMap = props.signalToPduMap;
		
		Object.keys(vssDB).forEach((key) => {
			this.getVssDBSubTree(vssDB[key], key);
		});

		Object.keys(vssAPI).forEach((key) => {
			this.getVssAPISubTree(vssAPI[key], key);
		});

		this.vssSignalMap.forEach((vss_signal, vss_name) => {
			if (vss_signal.mapping) {
				this.vssSignals.push(vss_name);
			}
		});
		
		this.vssSignals.sort();
		
		this.unsubscribe();
		setTimeout(() => {
			this.subscribe();
		}, 100);
	}
	
	componentWillUnmount() {
		//console.log("componentWillUnmount()");
		this.unsubscribe();
	}

	getVssDBSubTree(node, path) {
		const children = node['children'];
		if (node['type'] === 'branch') {
			Object.keys(children).forEach((key) => {
				this.getVssDBSubTree(children[key], path + "." + key);
			});
		}
		else {
			node.name = path;
			if (node.default) {
				node.value = node.default;
			}
			else {
				node.value = "undefined";
			}
			this.vssSignalMap.set(path, node);
			const canSig = node.mapping;
			if (canSig) {
				this.canToVssMap.set(canSig, path);
				this.vssToCanMap.set(path, canSig);
			}
		}
	}

	getVssAPISubTree(node, path) {
		if (!this.vssSignalMap.has(path)) {
			Object.keys(node).forEach((key) => {
				this.getVssAPISubTree(node[key], path + "." + key);
			});
		}
		else {
			if (this.vssToCanMap.has(path)) {
				this.vssApiMap.set(path, node);
			}
		}
	}

	subscribe() {
		const jsonData1 = {
			signals: [],
			channel: SIGNAL_VIEWER_CHANNEL,
			type: SIGNAL_TYPES.VSS
		};

		const jsonData2 = {
			signals: [],
			channel: SIGNAL_VIEWER_CHANNEL2,
			type: SIGNAL_TYPES.RAW
		};

		for (const name of this.vssSignals) {
			jsonData1.signals.push({
				name: name
			});

			if (this.vssToCanMap.has(name)) {
				const canSig = this.vssToCanMap.get(name);
				jsonData2.signals.push({
					name: canSig
				});
			}
		}

		subscribeSignals(jsonData1, (json) => {
			if (json.signals != null) {
				for (const signal of json.signals) {
					this.updateVssField(signal);
				}
			}
		});

		subscribeSignals(jsonData2, (json) => {
			if (json.signals != null) {
				for (const signal of json.signals) {
					this.updateCanField(signal);
				}
			}
		});
	}

	updateCanField(signal) {
		//console.log(signal);
		let tag = document.getElementById("physical_value_field_" + signal.name);
		tag.value = signal.value;

		const rawValue = String(physToRaw(signal.value, this.canSignalMap.get(signal.name)));
			
		tag = document.getElementById("raw_value_field_" + signal.name);
		tag.innerText = rawValue;

		tag = document.getElementById("value_desc_" + signal.name);
		if (tag) {
			const options = tag.getElementsByTagName("option");
			for (let i = 0; i < options.length; i++) {
				if (options[i].innerText.startsWith(rawValue)) {
					tag.selectedIndex = i;
					break;
				}
			}
		}
	}

	updateVssField(signal) {
		let tag = document.getElementById("vss_value_field_" + signal.name);
		if (tag) {
			tag.value = signal.value;
			tag = document.getElementById("vss_value_field2_" + signal.name);
			if (tag) {
				const signalNumVal = Number(signal.value);
				const allowed = this.vssApiMap.get(signal.name).allowed;
				for (const key of Object.keys(allowed)) {
					if (allowed[key] === signalNumVal) {
						tag.innerText = key;
					}
				}
			}
		}
		else {
			tag = document.getElementById("vss_value_combo_" + signal.name);
			if (tag) {
				const options = tag.getElementsByTagName("option");
				for (let i = 0; i < options.length; i++) {
					if (options[i].innerText === signal.value) {
						tag.selectedIndex = i;
						break;
					}
				}
			}
		}
	}

	unsubscribe() {
		unsubscribeSignals({ channel: SIGNAL_VIEWER_CHANNEL });
		unsubscribeSignals({ channel: SIGNAL_VIEWER_CHANNEL2 });
	};
	
	regenIndex(itemsFound) {
		for (let i = 0; i < itemsFound.length; i++) {
			let td = itemsFound[i].getElementsByTagName("td")[0];
			if (td) {
				td.innerText = i + 1;
			}
		}
	}

	render() {
		return (
			<div className="w3-margin-top w3-margin-left w3-margin-right">
				<div className="w3-row-padding">
					<div className="w3-third">
						<label className="w3-text-blue">Search:</label>
						<input className="w3-input w3-border w3-padding" type="text"
							placeholder="Search for names..." id="searchTarget"
							defaultValue={""}
							onKeyUp={() => {
								const itemsFound = searchItems("searchTarget", DEFAULT_SEARCH_ITEMS, EXTRA_SEARCH_ITEMS, EXTRA_SEARCH_INDICES); 
								this.regenIndex(itemsFound);
							}}
						/>
					</div>
				</div>
				<table className="w3-table-all w3-hoverable w3-margin-top MyStckyHeader" id="myTable">
					<thead>
						<tr className="w3-light-green MyStckyHeader">
							<th onClick={() => {sortTable(COLUMNS.INDEX, true);}} >
								Index
							</th>
							<th onClick={() => {sortTable(COLUMNS.VSS_KEY);}} >
								VSS Key
							</th>
							<th>
								VSS Value
							</th>
							<th onClick={() => {sortTable(COLUMNS.SIGNAL);}} >
								CAN Signal
							</th>
							<th>
								Physical Value
							</th>
							<th>
								Raw Value
							</th>
							<th>
								Value Descriptions
							</th>
							<th onClick={() => {sortTable(COLUMNS.INIT_VALUE, true);}} >
								Init Value
							</th>
							<th onClick={() => {sortTable(COLUMNS.TYPE);}} >
								Type
							</th>
							<th>
								Range
							</th>
							<th>
								Description
							</th>
							<th onClick={() => {sortTable(COLUMNS.SENDER);}} >
								Sender
							</th>
							<th>
								Receivers
							</th>
						</tr>
					</thead>
					<tbody>
						{
							this.vssSignals.map((name, index) => {
								const canSig = this.vssToCanMap.get(name);
								return(
									<EcuSignal key={name}
										index={index + 1}
										vss_name={name}
										vss_signal={this.vssSignalMap.get(name)}
										can_signal={this.canSignalMap.get(canSig)}
										can_pdu={this.canSignalToPduMap.get(canSig)}
										vss_api={this.vssApiMap.get(name)}
									/>
								);
							})
						}
					</tbody>
				</table>
			</div>
		);
	}
}

function EcuSignal(props) {
	const initPhysicalValue = props.can_signal.initValue;
	const initRawValue = physToRaw(initPhysicalValue, props.can_signal);
	
	const [physicalValue, setPhysicalValue] = useState(Number(initPhysicalValue));
	
	const physical_value_field_name = "physical_value_field_" + props.can_signal.name;
	const raw_value_field_name = "raw_value_field_" + props.can_signal.name;
	const value_desc_name = "value_desc_" + props.can_signal.name;

	const [vss_value, setVssValue] = useState(props.vss_signal.value);

	const receivers = [];
	
	if (props.can_signal.receiverAry != null && props.can_signal.receiverAry.length !== 0) {
		for (const receiver of props.can_signal.receiverAry) {
			receivers.push(receiver);
		}
	}
	receivers.sort();

	const updateCan = (name, value = physicalValue) => {
		let valueToSend = value;
		if (props.can_signal.length <= 32) {
			valueToSend = Number(value);
		}
		
		let jsonData = {
			signals: [ 
				{ 
					name  : name, 
					value : valueToSend
				} 
			],
			type: SIGNAL_TYPES.RAW
		};
		
		setSignals(jsonData);
	}

	const updateVss = (name, value = vss_value) => {
		const valueToSend = value;
		if (vss_value !== value) {
			setVssValue(value);
		}
		
		let jsonData = {
			signals: [ 
				{ 
					name  : name, 
					value : valueToSend
				} 
			],
			type: SIGNAL_TYPES.VSS
		};
		
		setSignals(jsonData);
	}

	function getRange(signal) {
		let min = 0;
		let max;
		if (signal.length <= 32) {
			const enumSizeMax = 1 << signal.length;
			const enumSize = signal.valueDescAry.length;

			if (signal.isSigned && enumSize < enumSizeMax) {
				if (signal.length < 32) {
					max = (1 << (signal.length - 1)) - 1;
					min = -(max + 1);
				}
				else {
					max = 0x7FFFFFFF;
					min = -(0x80000000);
				}
			}
			else {
				if (signal.length < 32) {
					max = (1 << signal.length) - 1;
				}
				else {
					max = 0xFFFFFFFF;
				}
			}

			if (signal.factor !== 1) {
				max *= signal.factor;
				min *= signal.factor;
			}
			if (signal.offset !== 0) {
				max += signal.offset;
				min += signal.offset;
			}
			if (signal.factor !== 1) {
				max = Number(Number(max).toFixed(5));
				min = Number(Number(min).toFixed(5));
			}
		}
		else {
			max = 0xFFFFFFFFFFFFFFFFn;
			let div = BigInt(1 << (64 - signal.length));
			max /= div;
		}

		let range = min + " ~ " + max;

		return range;
	}

	function setRawValue(value) {
		const rawVal = String(value).split(" : ")[0];
		let physVal = Number(rawVal) * Number(props.can_signal.factor) + Number(props.can_signal.offset);
		physVal = Number(physVal.toFixed(5));
		updateCan(props.can_signal.name, physVal);
	}

	function setRawValue2(event) {
		const options = event.target.getElementsByTagName("option");
		if (options.length === 1) {
			setRawValue(event.target.value);
		}
	}

	function renderVssValueField(vss_name, vss_signal) {
		if (vss_signal.datatype === "boolean") {
			return (
				<select 
					id={"vss_value_combo_" + vss_name}
					style={{ width: "100%" }}
					onChange={(e) => updateVss(vss_name, e.target.value) }
				>
					<option key="undefined">undefined</option>
					<option key="True">True</option>
					<option key="False">False</option>
				</select>
			);			
		}
		else if (vss_signal.allowed && vss_signal.allowed !== "-") {
			return (
				<select 
					id={"vss_value_combo_" + vss_name}
					style={{ width: "100%" }}
					onChange={(e) => updateVss(vss_name, e.target.value) }
				>
					<option key="undefined">undefined</option>
					{
						vss_signal.allowed.map((key) =>
							<option key={key}>
								{key}
							</option>
						)
					}
				</select>
			);
		}
		else {
			return (
				<>
					<input
						id={"vss_value_field_" + vss_name}
						style={{ width: "100%" }}
						type="text"
						defaultValue={vss_value}
						onChange={(e) => setVssValue(e.target.value)}
						onKeyDown={(e) => {
							if (e.key === 'Enter') {
								updateVss(vss_name);
							}
						}}>
					</input>
					{props.vss_api.allowed &&
						<span id={"vss_value_field2_" + vss_name} style={{color: "blue"}}>
						</span>
					}
				</>
			);
		}
	}

	return (
		<tr>
			<td>{props.index}</td>
			<td><b>{props.vss_name}</b></td>
			<td>
				{props.vss_name &&
					renderVssValueField(props.vss_name, props.vss_signal)
				}
			</td>
			<td><b>{props.can_signal.name}</b></td>
			<td>
				<input
					id={physical_value_field_name}
					style={{ width: "100%" }}
					type="text" 
					defaultValue={physicalValue}
					onChange={(e) => { setPhysicalValue(e.target.value); }}
					onKeyDown={(e) => {
						if (e.key === 'Enter') {
							updateCan(props.can_signal.name);
						}
					}}
				>
				</input>
			</td>
			<td id={raw_value_field_name}>{initRawValue}</td>
			{
				Object.keys(props.can_signal.valueDescAry).length > 0 ?
					<td>
						<select
							id={value_desc_name}
							style={{ width: "100%" }}
							onChange={(e) => { setRawValue(e.target.value) }}
							onFocus={(e) => { setRawValue2(e) }}
						>
							{
								Object.keys(props.can_signal.valueDescAry).map((key) =>
									<option key={key}>
										{key.substring(5) + " : " + props.can_signal.valueDescAry[key]}
									</option>
								)
							}
						</select>
					</td>
					:
					<td>-</td>
			}
			<td>{props.can_signal.initValue}</td>
			<td>{props.can_signal.apType}</td>
			<td>{getRange(props.can_signal)}</td>
			<td>{props.can_signal.desc}</td>
			<td>{props.can_pdu.sender}</td>
			{
				receivers.length !== 0 ?
					<td>
						<select style={{ width: "100%" }} >
							{receivers.map((receiver) =>
								<Receiver key={props.can_signal.name + "_" + receiver} receiver={receiver} />
							)}
						</select>
					</td>
					:
					<td>-</td>
			}
		</tr>
	);
}

function Receiver(props) {
	return (
		<option>{props.receiver}</option>
	);
}

function physToRaw(physVal, signal) {
	let rawVal = physVal;
	if (signal.factor > 0) {
		rawVal -= signal.offset;
		rawVal /= signal.factor;
		rawVal = Math.round(rawVal.toString());
	}
	return rawVal;
}

export default memo(SignalConversion);