import React from 'react';
import { BrowserRouter, Routes, Route } from "react-router-dom";

import Layout from "./pages/Layout";
import Overview from "./pages/Overview";
//import ECUs from "./pages/ECUs";
//import PDUs from "./pages/PDUs";
//import Signals from "./pages/Signals";
import NoPage from "./pages/NoPage";
import signalDB from "./pages/SignalDB.json";

import VssTree from './vehicle_api/VssTree';
import SignalConversion from "./vehicle_api/SignalConversion";
//import SignalTree from './vehicle_api/SignalTree';

import VhalProps from './vehicle_api/VhalProps';
import Login from './vehicle_api/Login';


function App() {

	const ecuMap = new Map();
	const pduMap = new Map();
	const signalMap = new Map();

	const senderMap = new Map();
	const receiverMap = new Map();
	const signalToPduMap = new Map();

	signalDB.pdus.forEach((pdu) => {
		//this.pdus.push(pdu);
		
		pdu.signals.forEach((signal) => {
			const receivers = [];
			if (signal.receiverAry != null && signal.receiverAry.length !== 0) {
				for (const receiver of signal.receiverAry) {
					receivers.push(receiver);
				}
			}
			receivers.sort();
			receiverMap.set(signal, receivers);
			
			ecuMap.set(pdu.sender, pdu.sender);
			signalToPduMap.set(signal.name, pdu);
			
			signalMap.set(signal.name, signal);
			senderMap.set(signal, pdu.sender);

			pduMap.set(pdu.name, pdu);
		});
	});

	return (
		<BrowserRouter>
			<Routes>
				<Route path="/" element={<Layout />}>
				<Route index element={<Overview signalDB={signalDB} />}/>
				{/*
				<Route path="ecus" element={<ECUs signalDB={signalDB} />} />
				<Route path="pdus" element={<PDUs signalDB={signalDB} />} />
				<Route 
					path="signals" 
					element={
						<Signals
							ecuMap={ecuMap} pduMap={pduMap} signalMap={signalMap}
							signalToPduMap={signalToPduMap}
							senderMap={senderMap} receiverMap={receiverMap}
						/>
					}
				/>
				*/}

				<Route 
					path="vss-tree" 
					element={
						<VssTree/>
					}
				/>
				<Route 
					path="signal-tree" 
					element={
						// <HiddenPage message={"Proprietary Raw CAN Signals"} />
						<Login signalMap={signalMap} senderMap={senderMap} />
					}
				/>
				<Route 
					path="signal-conversion" 
					element={
						<SignalConversion signalMap={signalMap} signalToPduMap={signalToPduMap} />
					}
				/>
				<Route 
					path="vhal" 
					element={
						<VhalProps/>
					}
				/>
				
				<Route path="*" element={<NoPage />} />
				</Route>
			</Routes>
		</BrowserRouter>
	);
}

export default App;
