/**
 * 
 */
import React from "react";
import { memo } from "react";
import "./SignalTree.css";
import vssDB from "./VssDB.json";
import { 
	SIGNAL_TYPES, SIGNAL_VIEWER_CHANNEL,
	setSignals, getSignals, 
	subscribeSignals, unsubscribeSignals 
} from "./WsReq";


class VssTree extends React.Component {

	selectedListItem = null;
	oldPath = null;
	
	constructor(props) {
        super(props);
		this.state = {
			path: "Vehicle",
			name: "Vehicle",
			type: vssDB.Vehicle.type ? vssDB.Vehicle.type : "-",
			description: vssDB.Vehicle.description ? vssDB.Vehicle.description : "-",
			datatype: "-",
			default: "-",
			min: "-",
			max: "-",
			unit: "-",
			comment: vssDB.Vehicle.comment ? vssDB.Vehicle.comment : "-",
			deprecation: vssDB.Vehicle.deprecation ? vssDB.Vehicle.deprecation : "-",
			allowed: "-"
		};
    }

	componentWillUnmount() {
		unsubscribeSignals({ channel: SIGNAL_VIEWER_CHANNEL });
	}

	handleTreeEvent(treeItem, node, path, name) {
		if (path !== 'Vehicle') {
			treeItem.parentElement.querySelector(".nested").classList.toggle("active");
			treeItem.classList.toggle("caret-down");
		}

		if (this.selectedListItem) {
			if (this.oldPath !== 'Vehicle') {
				this.selectedListItem.setAttribute("style", "color: black; cursor: pointer;");
			}
			else {
				this.selectedListItem.setAttribute("style", "color: black; font-size: 22px; cursor: pointer;");
			}
		}

		if (path !== 'Vehicle') {
			treeItem.setAttribute("style", "color: blue; font-weight: 700; cursor: pointer;");
		}
		else {
			treeItem.setAttribute("style", "color: blue; font-size: 22px; font-weight: 700; cursor: pointer;");
		}

		this.selectedListItem = treeItem;
		this.oldPath = path;

		this.setState({
			path: path,
			name: name,
			type: node.type ? node.type : "-",
			description: node.description ? node.description : "-",
			datatype: "-",
			default: "-",
			min: "-",
			max: "-",
			unit: "-",
			comment: node.comment ? node.comment : "-",
			deprecation: node.deprecation ? node.deprecation : "-",
			allowed: "-",
		});
	}

	getList(node, path) {
		const children = node['children'];
		return (
			<ul>
				{Object.keys(children).map((key) => {
					if ((children[key])['type'] === "branch") {
						return this.getSubTree(children[key], path + "." + key, key);
					}
					else {
						return null;
					}
				})}
				{Object.keys(children).map((key) => {
					if ((children[key])['type'] !== "branch") {
						return this.getSubTree(children[key], path + "." + key, key);
					}
					else {
						return null;
					}
				})}
			</ul>
		);
	}

	getSignalCount(node) {
		let size = 0;
		const children = node['children'];
		if (children) {
			Object.values(children).forEach((subnode) => {
				size += this.getSignalCount(subnode);
			});
		}
		else {
			size = 1;
		}
		return size;
	}

	getSubTree(node, path, name) {
		const children = node['children'];
		return (
			<li key={path}>
				{node['type'] === 'branch' ?
					<>
						<span className="caret" onClick={(e) => this.handleTreeEvent(e.target, node, path, name)} >
							{name}
						</span>
						<span>
							&nbsp;({this.getSignalCount(node)})
						</span>
						<ul className="nested">
							{Object.keys(children).map((key) => {
								if ((children[key])['type'] === "branch") {
									return this.getSubTree(children[key], path + "." + key, key);
								}
								else {
									return null;
								}
							})}
							{Object.keys(children).map((key) => {
								if ((children[key])['type'] !== "branch") {
									return this.getSubTree(children[key], path + "." + key, key);
								}
								else {
									return null;
								}
							})}
						</ul>
					</>
					:
					<>
						<span onClick={(e) => this.handleSignal(e.target, node, path, name)} style={{ cursor: "pointer" }} >
							{name}
						</span>
					</>
				}
			</li>
		);
	}

	handleSignal(listItem, node, path, name) {

		if (this.selectedListItem) {
			this.selectedListItem.setAttribute("style", "color: black; cursor: pointer;");
		}
		listItem.setAttribute("style", "color: blue; font-weight: 700; cursor: pointer;");
		this.selectedListItem = listItem;

		let defaultValue = null;
		if (node.default) {
			defaultValue = String(node.default);
		}

		this.setState({
			path: path,
			name: name,
			type: node.type ? node.type : "-",
			description: node.description ? node.description : "-",
			datatype: node.datatype ? node.datatype : "-",
			default: defaultValue ? defaultValue : "-",
			min: node.min ? node.min : "-",
			max: node.max ? node.max : "-",
			unit: node.unit ? node.unit : "-",
			comment: node.comment ? node.comment : "-",
			deprecation: node.deprecation ? node.deprecation : "-",
			allowed: node.allowed ? node.allowed : "-"
		});

		unsubscribeSignals({ channel: SIGNAL_VIEWER_CHANNEL });
		const jsonData = {
			channel: SIGNAL_VIEWER_CHANNEL,
			type: SIGNAL_TYPES.VSS,
			signals: [
				{ 
					name: path
				}
			]
		};
		subscribeSignals(
			jsonData,
			(json) => {
				if (json && json.signals && json.signals.length > 0) {
					const signal = json.signals[0];
					if (signal.name === path) {
						this.updateVssField(signal);
					}	
				}
			});
	}

	getAllowedList() {
		if (this.state.allowed === "-") {
			return "-";
		}
		else {
			if (this.state.allowed && this.state.allowed.length !== 0) {
				return (
					<select style={{width: "100%"}} >
						{this.state.allowed.map((label) => 
							<option key={label}>{label}</option>
						)}
					</select>
				);
			}
			else {
				return "-";
			}
		}
	}

	setVssValue(path) {
		let value = null;
		let tag = document.getElementById("vss-value-text");
		if (tag) {
			value = tag.value;
		}
		else {
			tag = document.getElementById("vss-value-combo");
			if (tag) {
				const options = tag.getElementsByTagName("option");
				if (options && options.length > 1) {
					if (tag.selectedIndex > 0 && tag.selectedIndex < options.length) {
						value = options[tag.selectedIndex].innerText;
					}
				}
			}
		}
		if (value != null) {
			const jsonData = {
				signals: [
					{ 
						name  : path, 
						value : value
					}
				],
				type: SIGNAL_TYPES.VSS
			};
			//console.log(jsonData.signals[0]);
			setSignals(jsonData);
		}
	}

	getVssValue(path) {
		const jsonData = {
			signals: [
				{ 
					name: path
				}
			],
			type: SIGNAL_TYPES.VSS
		};
		getSignals(
			jsonData, 
			(json) => {
				if (json && json.signals && json.signals.length > 0) {
					const signal = json.signals[0];
					if (signal.name === path) {
						this.updateVssField(signal);
					}
				}
			});
	}

	updateVssField(signal) {
		this.setState({
			value: signal.value
		});
		let tag = document.getElementById("vss-value-text");
		if (tag) {
			tag.value = signal.value;
		}
		else {
			tag = document.getElementById("vss-value-combo");
			if (tag) {
				const options = tag.getElementsByTagName("option");
				for (let i = 0; i < options.length; i++) {
					if (options[i].innerText === signal.value) {
						tag.selectedIndex = i;
						break;
					}
				}
			}
		}
	}

	renderVssValueField() {
		if (this.state.datatype === "boolean") {
			return (
				<select id="vss-value-combo"
					onChange={() => this.setVssValue(this.state.path) }
				>
					<option key="undefined">undefined</option>
					<option key="True">True</option>
					<option key="False">False</option>
				</select>
			);			
		}
		else if (this.state.allowed && this.state.allowed !== "-") {
			return (
				<select id="vss-value-combo"
					onChange={() => this.setVssValue(this.state.path) }
				>
					<option key="undefined">undefined</option>
					{
						this.state.allowed.map((key) =>
							<option key={key}>
								{key}
							</option>
						)
					}
				</select>
			);
		}
		else {
			return (
				<input type="text" defaultValue={this.state.value} id="vss-value-text"
					onKeyDown={(e) => {
						if (e.key === 'Enter') {
							this.setVssValue(this.state.path);
						}
					}}>
				</input>
			);
		}
	}
   
	render() {
		return (
			<div className="w3-row-padding w3-margin-top">
				<div className="w3-col w3-container" style={{ width: "40%" }}>
					<ul id="myUL">
						<li key={"Vehicle"} >
							<span
								onClick={(e) => this.handleTreeEvent(e.target, vssDB.Vehicle, "Vehicle")}
								style={{ cursor: "pointer", fontSize: "22px" }}
							>
								{"\u25B6"}{"Vehicle"}
							</span>
							<span>
								&nbsp;({this.getSignalCount(vssDB.Vehicle)})
							</span>
							{this.getList(vssDB.Vehicle, "Vehicle")}
						</li>
					</ul>
				</div>
				<div className="w3-col w3-container MyStckyHeader" style={{width:"60%"}}>
					<div className="w3-card-4">
						<header className="w3-container w3-blue">
							<h2>{this.state.path}</h2>
						</header>

						<div className="w3-container">
							<table className="w3-table">
								<tbody>
								<tr>
									<td><b>path</b></td>
									<td><b>{this.state.path}</b></td>
								</tr>
								<tr>
									<td><b>description</b></td>
									<td>{this.state.description}</td>
								</tr>
								<tr>
									<td><b>type</b></td>
									<td>{this.state.type}</td>
								</tr>
								<tr>
									<td><b>datatype</b></td>
									<td>{this.state.datatype}</td>
								</tr>
								<tr>
									<td><b>default</b></td>
									<td>{this.state.default}</td>
								</tr>
								<tr>
									<td><b>min</b></td>
									<td>{this.state.min}</td>
								</tr>
								<tr>
									<td><b>max</b></td>
									<td>{this.state.max}</td>
								</tr>
								<tr>
									<td><b>unit</b></td>
									<td>{this.state.unit}</td>
								</tr>
								<tr>
									<td><b>allowed</b></td>
									<td>{this.getAllowedList()}</td>
								</tr>
								<tr>
									<td><b>comment</b></td>
									<td>{this.state.comment}</td>
								</tr>
								<tr>
									<td><b>deprecation</b></td>
									<td>{this.state.deprecation}</td>
								</tr>
								{ this.state.type !== "branch" &&
								<tr>
									<td><b>Current Value</b></td>
									{ this.state.type === "branch" ?
									<td>-</td>
									:
									<td>
										{ this.renderVssValueField() }
										<span>&emsp;</span>
										<button onClick={() => this.setVssValue(this.state.path)} >
											Set
										</button>
										<span>&emsp;</span>
										<button onClick={() => this.getVssValue(this.state.path)} >
											Get
										</button>
									</td>
									}
								</tr>
								}
								</tbody>
							</table>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default memo(VssTree);